body{
  background-color: black;
}
.app-header{
  display: flex;
  color: white;
  justify-content: center;
}
.form-div{
  display: flex;
  justify-content: center;
}
.form-div .input-div{
  display: flex;
}
.form-div input{
  font-size: 30px;
}
.form-div button{
  border: none;
  font-size: 20px;
}
.list-div ul{
  display: flex;
  flex-direction: column;
  margin-right: 25%;
  margin-left: 25%;
}
.todo-div li{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.todo-div li div{
  display: flex;
  margin: 4px;
}
.todo-div li button{
  outline: none;
  border: none;
  margin: 4px;
  font-size: 20px;
}
.completed{
  background-color: greenyellow;
}
.todo-div li p{
  font-size: 20px;
  color: white;
  /* width: 30px; */
}
#complete{
  background-color: white;
}
#delete{
  background-color: tomato;
}
